@import "_grid/vars";
@import "_grid/functions";

@function grid-column-width($n) {
  @return percentage(100 / 12 * $n) / 100 - 4%;
}

.container {
  width: 98%;
  max-width: 960px;
  margin: 0 auto;
}

.row {
  @extend %clearfix;
  background-color: cyan;
  margin-bottom: 1.1rem;
}

.column {
  min-height: 1px;
  float: left;
  background-color: pink;
}

// Small Grid - Default Grid
@for $i from 1 through 12 {
  .sm-#{$i}.column {
    width: grid-column-width($i);
    margin-right: 2%;
    margin-left: 2%;
  }
}

